    description: "Marc-Andr ter Stegen urodzi si 30 kwietnia 1992 roku w niemieckim Mnchengladbach. Ju od najmodszych lat by wielk nadziej niemieckiego futbolu, jeli chodzi o pozycj bramkarza. Przed podpisaniem kontraktu z FC Barcelon 22 maja 2014 roku, przez 18 lat wystpowa w Borussii Mnchengladbach. Trafi do druyn modzieowych tego klubu w wieku zaledwie 4 lat, a jak tylko osign penoletnio, zadebiutowa w pierwszym zespole. Byo to dokadnie 10 kwietnia 2011 roku w meczu 29. kolejki Bundesligi z FC Kln, ktry Borussia Mnchengladbach wygraa 5:1. W pierwszych szeciu spotkaniach w lidze niemieckiej ter Stegen puci zaledwie trzy gole i stopniowo umacnia swoj pozycj w bramce niemieckiej druyny. Bardzo szybko na ter Stegena spada dua odpowiedzialno, gdy jego sidmy i smy mecz w barwach dorosego zespou Borussii przypady na barae o utrzymanie w najwyszej klasie rozgrywkowej w Niemczech. Zawodnik wietnie poradzi sobie w tych spotkaniach i by jednym z architektw sukcesu - wygranej w dwumeczu z Bochum 2:1. Marc-Andr ter Stegen na stae zagoci w bramce Borussii Mnchengladbach na kolejne trzy sezony. W pierwszym z nich zosta wybrany najlepszym bramkarzem Bundesligi. W lidze niemieckiej rozegra w sumie 108 meczw. W Barcelonie ju od samego pocztku wici triumfy z zespoem, bdc podstawowym bramkarzem w rozgrywkach Ligi Mistrzw oraz Pucharu Krla. Ter Stegen jest drugim niemieckim bramkarzem w historii FC Barcelony po Robercie Enke.",
