    name: "Jordi Alba",
    position: "defender",
    description: "Jordi Alba i Ramos urodzi si w kataloskim L'Hospitalet de Llobregat 21 marca 1989 roku. Po tym jak trafi do Barcelony majc 10 lat, przeszed nastpnie przez kilka modzieowych zespow, grajc m.in. z takimi pikarzami jak Bojan Krkic, Fran Mrida czy Dos Santos. W 2007 roku opuci Barcelon i trafi do UE Cornell, skd odszed po roku i podpisa kontrakt z Valenci., ktra zapacia za niego... 6 tysicy euro. Najpierw gra w modzieowym zespole, po czym zosta wczony do Valencia CF Mestalla - druyny, ktra wywalczya awans do Segona Divisi B. Alba by wwczas kluczow jej postaci, a w midzyczasie zadebiutowa w reprezentacji Hiszpanii do lat 19. Sezon 2008/09 spdzi na wypoyczeniu w Gimnstic de Tarragona, rozgrywajc 30 meczw w Segunda Divisin, grajc take dla kadry U-21 i bdc w skadzie reprezentacji U-20 na mistrzostwa wiata rozgrywane w Egipcie. Potem wrci do Valencii, stajc si jej podstawowym pikarzem. W La Liga zadebiutowa 13 wrzenia 2009 roku przeciwko Realowi Valladolid. Mimo e nominalnie gra na lewej obronie, to pokazywa swoj wszechstronno grajc na rnych pozycjach. Zanim trafi do Barcelony, w barwach Valencii zagra 108 razy - w tym a 48 w sezonie 2011/12. Jego znakomita postawa, we wrzeniu 2011 roku, zaowocowaa powoaniem do seniorskiej reprezentacji Hiszpanii. W kadrze Vicente del Bosque zadebiutowa 11 padziernika meczem przeciwko Szkocji. Pl roku pniej by ju czonkiem druyny na turniej Euro 2012, gdzie odegra kluczow rol, bdc podstawowym zawodnikiem reprezentacji.Jordi Alba kontrakt z Barcelon podpisa 5 lipca 2012 roku, po tym jak FC Barcelona osigna porozumienie z Valenci odnonie jego transferu. Mia wwczas 23 lata i sta si pierwszym nabytkiem Tito Vilanovy przed sezonem 2012/13. Przez kolejne dwa sezony by podstawowym obroc druyny Barcelony i przyczyni si do zdobycia tytuu mistrzowskiego.",
