    description: "Philippe Coutinho urodzi si 12 czerwca 1992 roku w Rio de Janeiro. Obecnie jest jednym z najbardziej utalentowanych pomocnikw na wiecie. Brazylijczyk swoje umiejtnoci zdy pokaza ju chociaby w reprezentacji Brazylii oraz Liverpoolu. Coutinho karier rozpocz w Vasco da Gama, skd przenis si do Europy. W 2010 roku, w wieku zaledwie 16 lat zwiza si z Interem. Wwczas jako 18-latek zadebiutowa w Lidze Mistrzw. Po krtkiej przygodzie w Espanyolu trafi do Liverpoolu. W angielskim klubie Brazylijczyk spdzi 5 lat. 6 stycznia 2018 roku Barcelona poinformowaa o zakupie pikarza. Coutinho 5,5-letni kontrakt z kataloskim klubem podpisa dwa dni pniej i zosta najdroszym pikarzem kupionym przez Blaugran. Klauzula wykupu zostaa ustalona na poziomie 400 mln euro.",
