import React from 'react';
import Article from '../Components/Article';

const articles = [
    {
        id: 1,
        title: "Historii klubu",
        text: "W dziale powiconym historii moesz dowiedzie si jak wyglday pocztki wielkiej FC Barcelony."
    },
    {
        id: 2,
        title: "Kadrze klubu",
        text: "W tym dziale moesz zapozna si z aktualn kadr Dumy Katalonii oraz przeczyta rne informacje na temat kadego zawodnika."
    },
    {
        id: 3,
        title: "Sukcesach klubu",
        text: "Jeli ciekawi ci ile oraz jakie tytuy zdobya FC Barcelona, zajrzyj do sekcji powiconej sukcesom klubu."
    }
]

const HomePage = () => {

    const artList = articles.map(article => (
        <Article key={article.id} {...article} />
    ))
    return (
        <div className="home">
            <h1>
            <p>Witaj na stronie powiconej klubowi pikarskiemu FC Barcelona!</p></h1>
            <br></br>
            <p>Tutaj moesz dowiedzie si o:</p>
            {artList}
        </div>
    );
}

export default HomePage;