import React from 'react';

const HistoryPage = () => {
    return (
        <div className="history">
            <h1 className="title">HISTORIA</h1>
            <br></br>
            <p>Korzenie FC Barcelony sigaj 1899 roku, kiedy to 29 listopada Joan Gamper postanowi wraz z grup swoich przyjaci utworzy klub Foot-Ball Club Barcelona. Cho klub powsta w sercu Katalonii, atwo zauway angielskie pochodzenie jego nazwy. Nie by to przypadek, albowiem zarzd pochodzi z Wielkiej Brytanii. Prezesem zosta Walter Wild, a oprcz niego podpisy na akcie zaoycielskim zoyli: wspomniany ju Joan Gamper (znany te jako Hans Kamper), Llus dOss, Bartomeu Terradas, Otto Maier, Otto Kunzle, Pedro Cabot, Enric Ducal, Jose Loobet, Carlos Pujol oraz John i William Parsons. Funkcj skarbnika pocztkowo piastowa Bartomeu Terradas, a na sekretarza wybrano Llusa dOss. Zaoyciele zdecydowali si na bordowo-granatowe barwy, ktre byy inspirowane klubem z Bazylei. Herbem klubu by po prostu herb miasta.</p>
            <br></br>
            <p>5 grudnia 1899 roku do zarzdu doczyy kolejne osoby. Ogoszono te kandydata na wiceprezesa, ktrym zosta John Parsons. Z kolei William Parsons zosta czonkiem druyny, w ktrej peni funkcj drugiego kapitana.</p>
            <br></br>
            <p>Pierwszy mecz FC Barcelony odby si 8 grudnia 1899 roku, czyli zaledwie kilka dni po podpisaniu aktu zaoycielskiego. Na tym icie historycznym spotkaniu Hiszpanie zmierzyli si z Anglikami, ktrzy pokonali ich 1:0. Konfrontacja miaa miejsce na Velodromo de la Bonanova, pierwszym boisku Barcy. Relacj z tego meczu mona byo przeczyta 9 grudnia w dzienniku La Vanguardia. W ramach ciekawostki mona doda, e w skadzie kadej druyny znajdowao si wtedy po dziesiciu zawodnikw.</p>
            <br></br>
            <p>Pierwszy wygrany mecz Barca wpisaa na swoje konto 25 grudnia 1899 roku, gdy w spotkaniu z miejscow druyn Catala FC zwyciya 3:1. Jaki czas pniej zarzd zdecydowa, e klub musi mie wasne boisko, wobec czego wydzierawiono stadion znajdujcy si niedaleko popularnego hotelu Casanovas.</p>
            <p>23 grudnia 1900 roku odbyy si pierwsze derby Katalonii, a FC Barcelona zmierzya si ze studenckim zespoem Sociedad Espaola de Futbol. Mecz zakoczy si wynikiem 0:0.</p>
            <br></br>
            <p>W 1901 roku Barca wzia udzia w turnieju, w ktrym puchar fundowany by przez Alfonso Macaya. Zaja w nim drugie miejsce. Jednak przy okazji kolejnej edycji zwyciya cay turniej, tracc tylko dwie bramki, a strzelajc a szedziesit.</p>
            <br></br>
            <p>Pierwszy mecz na wyjedzie FC Barcelona rozegraa 13 maja 1902 roku. Jej przeciwnikiem by nowy wwczas klub Madrid FC (dzi znany wszystkim kibicom jako Real Madryt). Spotkanie zakoczyo si zwycistwem Dumy Katalonii.</p>
            <br></br>
            <p>Poza granicami Hiszpanii Barca zagraa po raz pierwszy 1 maja 1904 roku. By to wyjazd do Francji. Duma Katalonii spotkaa si na Stade Olympique z miejscowym zespoem Stade O.E.T, ktry pokonaa 3:2.</p>
            <br></br>
            <p>W 1905 roku FC Barcelona wygraa Puchar Katalonii, w finale zwyciajc z Espanyolem 3:2.</p>
            <br></br>
            <p>Cho Barca odnosia sukcesy, nie brakowao momentw, w ktrych wszyscy byli przekonani, e klub upadnie. Znaczcy wpyw na krytyczn sytuacj hiszpaskiego klubu miaa malejca liczba socios, czyli kibicw bdcych jednoczenie czonkami klubu i opacajcych specjalne skadki. W 1908 roku Barca miaa ju tylko 38 socios. Wtedy Jaon Gamper przesta by kapitanem klubu, zakoczy swoj karier sportow i doczy do zarzdu klubu. Zosta w nim prezesem na picioletni kadencj. Gamper sprawi, e sytuacja Barcy bardzo szybko ulega poprawie. Od 14 marca 1909 roku druyna zacza rozgrywa mecze na wykupionym na wasno stadionie Camp del Carrer Indstria.</p>
            <br></br>
            <p>W sezonie 1909/1910 Barca po raz kolejny signa po mistrzostwo Katalonii oraz zdobya Puchar Hiszpanii. Wobec tak wietnej formy druyny do klubu zaczy wpywa kandydatury na socios. W kolejnym sezonie FC Barcelona miaa ich ju 367.</p>
        </div>
    );
}

export default HistoryPage;